"use client"

import { useState, useEffect } from 'react'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { Dialog, DialogContent, DialogDescription, DialogFooter, DialogHeader, DialogTitle, DialogTrigger } from "@/components/ui/dialog"
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table"
import { Badge } from "@/components/ui/badge"
import { Separator } from "@/components/ui/separator"
import { useForm } from "react-hook-form"
import { zodResolver } from "@hookform/resolvers/zod"
import * as z from "zod"

const invoiceItemSchema = z.object({
  productId: z.string().min(1, "Please select a product"),
  quantity: z.number().min(1, "Quantity must be at least 1")
})

const invoiceSchema = z.object({
  customerId: z.string().min(1, "Please select a customer"),
  items: z.array(invoiceItemSchema).min(1, "At least one item is required")
})

type InvoiceFormData = z.infer<typeof invoiceSchema>

interface Product {
  id: string
  name: string
  price: number
  quantity: number
  gstRate: number
  category: string
}

interface Customer {
  id: string
  name: string
  email: string
  phone: string
  address: string
}

interface InvoiceItem {
  productId: string
  productName: string
  quantity: number
  price: number
  gstRate: number
  amount: number
  gstAmount: number
  total: number
}

interface Invoice {
  id: string
  invoiceNumber: string
  customerId: string
  customerName: string
  date: string
  items: InvoiceItem[]
  subtotal: number
  totalGST: number
  total: number
  gstAmount: number
}

export default function BillingSystem() {
  const [invoices, setInvoices] = useState<Invoice[]>([])
  const [products, setProducts] = useState<Product[]>([])
  const [customers, setCustomers] = useState<Customer[]>([])
  const [isDialogOpen, setIsDialogOpen] = useState(false)
  const [selectedCustomer, setSelectedCustomer] = useState<string>("")
  const [invoiceItems, setInvoiceItems] = useState<{productId: string, quantity: number}[]>([])
  const [searchTerm, setSearchTerm] = useState("")

  useEffect(() => {
    // Load data from localStorage
    const savedInvoices = localStorage.getItem('invoices')
    const savedProducts = localStorage.getItem('products')
    const savedCustomers = localStorage.getItem('customers')
    
    if (savedInvoices) setInvoices(JSON.parse(savedInvoices))
    if (savedProducts) setProducts(JSON.parse(savedProducts))
    if (savedCustomers) setCustomers(JSON.parse(savedCustomers))
  }, [])

  useEffect(() => {
    // Save invoices to localStorage
    localStorage.setItem('invoices', JSON.stringify(invoices))
  }, [invoices])

  const generateInvoiceNumber = () => {
    const date = new Date()
    const year = date.getFullYear()
    const month = String(date.getMonth() + 1).padStart(2, '0')
    const day = String(date.getDate()).padStart(2, '0')
    const sequence = String(invoices.length + 1).padStart(4, '0')
    return `INV-${year}${month}${day}-${sequence}`
  }

  const addInvoiceItem = () => {
    setInvoiceItems([...invoiceItems, { productId: "", quantity: 1 }])
  }

  const removeInvoiceItem = (index: number) => {
    setInvoiceItems(invoiceItems.filter((_, i) => i !== index))
  }

  const updateInvoiceItem = (index: number, field: 'productId' | 'quantity', value: string | number) => {
    const updatedItems = [...invoiceItems]
    updatedItems[index] = { ...updatedItems[index], [field]: value }
    setInvoiceItems(updatedItems)
  }

  const calculateInvoiceTotal = () => {
    let subtotal = 0
    let totalGST = 0
    
    const calculatedItems: InvoiceItem[] = invoiceItems.map(item => {
      const product = products.find(p => p.id === item.productId)
      if (!product) return null
      
      const amount = product.price * item.quantity
      const gstAmount = (amount * product.gstRate) / 100
      const total = amount + gstAmount
      
      subtotal += amount
      totalGST += gstAmount
      
      return {
        productId: product.id,
        productName: product.name,
        quantity: item.quantity,
        price: product.price,
        gstRate: product.gstRate,
        amount,
        gstAmount,
        total
      }
    }).filter(Boolean) as InvoiceItem[]
    
    return {
      items: calculatedItems,
      subtotal,
      totalGST,
      grandTotal: subtotal + totalGST
    }
  }

  const createInvoice = () => {
    if (!selectedCustomer || invoiceItems.length === 0) {
      alert('Please select a customer and add at least one item')
      return
    }

    const customer = customers.find(c => c.id === selectedCustomer)
    if (!customer) return

    const calculation = calculateInvoiceTotal()
    
    const newInvoice: Invoice = {
      id: Date.now().toString(),
      invoiceNumber: generateInvoiceNumber(),
      customerId: selectedCustomer,
      customerName: customer.name,
      date: new Date().toISOString(),
      items: calculation.items,
      subtotal: calculation.subtotal,
      totalGST: calculation.totalGST,
      total: calculation.grandTotal,
      gstAmount: calculation.totalGST
    }

    // Update product quantities
    const updatedProducts = products.map(product => {
      const invoiceItem = calculation.items.find(item => item.productId === product.id)
      if (invoiceItem) {
        return {
          ...product,
          quantity: product.quantity - invoiceItem.quantity
        }
      }
      return product
    })
    
    setProducts(updatedProducts)
    localStorage.setItem('products', JSON.stringify(updatedProducts))
    
    setInvoices([...invoices, newInvoice])
    setIsDialogOpen(false)
    setSelectedCustomer("")
    setInvoiceItems([])
  }

  const filteredInvoices = invoices.filter(invoice =>
    invoice.invoiceNumber.toLowerCase().includes(searchTerm.toLowerCase()) ||
    invoice.customerName.toLowerCase().includes(searchTerm.toLowerCase())
  )

  const formatCurrency = (amount: number) => {
    return new Intl.NumberFormat('en-IN', {
      style: 'currency',
      currency: 'INR'
    }).format(amount)
  }

  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString('en-IN')
  }

  const calculation = calculateInvoiceTotal()

  return (
    <div className="space-y-6">
      <div className="flex items-center justify-between">
        <div>
          <h2 className="text-3xl font-bold tracking-tight">Billing System</h2>
          <p className="text-muted-foreground">
            Create and manage invoices with GST calculations
          </p>
        </div>
        <Dialog open={isDialogOpen} onOpenChange={setIsDialogOpen}>
          <DialogTrigger asChild>
            <Button onClick={() => {
              setSelectedCustomer("")
              setInvoiceItems([])
            }}>
              Create Invoice
            </Button>
          </DialogTrigger>
          <DialogContent className="sm:max-w-[800px] max-h-[80vh] overflow-y-auto">
            <DialogHeader>
              <DialogTitle>Create New Invoice</DialogTitle>
              <DialogDescription>
                Generate a new invoice with GST calculations
              </DialogDescription>
            </DialogHeader>
            
            <div className="space-y-6">
              {/* Customer Selection */}
              <div>
                <Label>Select Customer</Label>
                <Select value={selectedCustomer} onValueChange={setSelectedCustomer}>
                  <SelectTrigger>
                    <SelectValue placeholder="Choose a customer" />
                  </SelectTrigger>
                  <SelectContent>
                    {customers.map(customer => (
                      <SelectItem key={customer.id} value={customer.id}>
                        {customer.name} - {customer.phone}
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </div>

              {/* Invoice Items */}
              <div>
                <div className="flex items-center justify-between mb-4">
                  <Label>Invoice Items</Label>
                  <Button type="button" variant="outline" onClick={addInvoiceItem}>
                    Add Item
                  </Button>
                </div>
                
                <div className="space-y-4">
                  {invoiceItems.map((item, index) => (
                    <div key={index} className="flex items-center space-x-4 p-4 border rounded">
                      <div className="flex-1">
                        <Select 
                          value={item.productId} 
                          onValueChange={(value) => updateInvoiceItem(index, 'productId', value)}
                        >
                          <SelectTrigger>
                            <SelectValue placeholder="Select product" />
                          </SelectTrigger>
                          <SelectContent>
                            {products.filter(p => p.quantity > 0).map(product => (
                              <SelectItem key={product.id} value={product.id}>
                                {product.name} - {formatCurrency(product.price)} (Stock: {product.quantity})
                              </SelectItem>
                            ))}
                          </SelectContent>
                        </Select>
                      </div>
                      <div className="w-24">
                        <Input
                          type="number"
                          min="1"
                          value={item.quantity}
                          onChange={(e) => updateInvoiceItem(index, 'quantity', parseInt(e.target.value) || 1)}
                          placeholder="Qty"
                        />
                      </div>
                      <Button
                        type="button"
                        variant="destructive"
                        size="sm"
                        onClick={() => removeInvoiceItem(index)}
                      >
                        Remove
                      </Button>
                    </div>
                  ))}
                </div>
              </div>

              {/* Invoice Summary */}
              {calculation.items.length > 0 && (
                <div className="border rounded p-4">
                  <h3 className="font-semibold mb-4">Invoice Summary</h3>
                  <div className="space-y-2">
                    {calculation.items.map((item, index) => (
                      <div key={index} className="flex justify-between text-sm">
                        <span>{item.productName} x {item.quantity}</span>
                        <span>{formatCurrency(item.total)}</span>
                      </div>
                    ))}
                    <Separator />
                    <div className="flex justify-between">
                      <span>Subtotal:</span>
                      <span>{formatCurrency(calculation.subtotal)}</span>
                    </div>
                    <div className="flex justify-between">
                      <span>GST:</span>
                      <span>{formatCurrency(calculation.totalGST)}</span>
                    </div>
                    <div className="flex justify-between font-bold">
                      <span>Total:</span>
                      <span>{formatCurrency(calculation.grandTotal)}</span>
                    </div>
                  </div>
                </div>
              )}
            </div>

            <DialogFooter>
              <Button onClick={createInvoice} disabled={!selectedCustomer || invoiceItems.length === 0}>
                Create Invoice
              </Button>
            </DialogFooter>
          </DialogContent>
        </Dialog>
      </div>

      {/* Search */}
      <div className="flex items-center space-x-4">
        <div className="flex-1">
          <Input
            placeholder="Search invoices..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            className="max-w-sm"
          />
        </div>
        <div className="flex space-x-4 text-sm text-muted-foreground">
          <span>Total Invoices: {invoices.length}</span>
          <span>Total Revenue: {formatCurrency(invoices.reduce((sum, inv) => sum + inv.total, 0))}</span>
        </div>
      </div>

      {/* Invoices Table */}
      <Card>
        <CardHeader>
          <CardTitle>Invoices</CardTitle>
          <CardDescription>
            All generated invoices
          </CardDescription>
        </CardHeader>
        <CardContent>
          {filteredInvoices.length === 0 ? (
            <div className="text-center py-8">
              <p className="text-muted-foreground">No invoices found</p>
              <p className="text-sm text-muted-foreground">
                {searchTerm ? 'Try adjusting your search' : 'Create your first invoice to get started'}
              </p>
            </div>
          ) : (
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead>Invoice #</TableHead>
                  <TableHead>Customer</TableHead>
                  <TableHead>Date</TableHead>
                  <TableHead>Items</TableHead>
                  <TableHead>Subtotal</TableHead>
                  <TableHead>GST</TableHead>
                  <TableHead>Total</TableHead>
                  <TableHead>Status</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {filteredInvoices.map((invoice) => (
                  <TableRow key={invoice.id}>
                    <TableCell className="font-medium">{invoice.invoiceNumber}</TableCell>
                    <TableCell>{invoice.customerName}</TableCell>
                    <TableCell>{formatDate(invoice.date)}</TableCell>
                    <TableCell>{invoice.items.length} items</TableCell>
                    <TableCell>{formatCurrency(invoice.subtotal)}</TableCell>
                    <TableCell>{formatCurrency(invoice.totalGST)}</TableCell>
                    <TableCell className="font-medium">{formatCurrency(invoice.total)}</TableCell>
                    <TableCell>
                      <Badge variant="default">Paid</Badge>
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          )}
        </CardContent>
      </Card>
    </div>
  )
}
